*{
  padding: 0;
  margin: 0;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

.nav {
  background-color: #ffd542;
  font-family: 'Sen', sans-serif;
  
}

.navbar-light .navbar-nav .nav-link {
  color: #271e85!important;
}

.background-white {
    background-color: #ffffff;
  }
  
  .background-blue {
    background-color: #2d98da;
  }

.subtitle {
    font-family: 'Sen', sans-serif;
    padding: 30px;
    text-align: center;
    font-size: 50px;
    color: #033e99;
}

.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hi {
  font-family: 'Sen', sans-serif;
  padding: 30px;
  color: #033e99;
  text-align: center;
}

.im {
  font-family: 'Sen', sans-serif;
  padding: 30px;
  font-size: 50px;
  color: #033e99;
  text-align: center;
}

.front {
  font-family: 'Sen', sans-serif;
  padding: 30px;
  color: #033e99;
  text-align: center;
}

.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profileText {
  margin-left: 25%;
  margin-right: 25%;
  padding: 25px;
  text-align: center;
  background-color: #3e35a2;
  color:  #ffeba8;
  font-family: 'Sen', sans-serif;
  font-size: 18px;
  border-radius: 20px;
}

.projectContainer {
display: flex;
flex-wrap: wrap;
justify-content: center;
margin-bottom: 30px;
}

.projectFull {
  margin: 10px;
  flex-wrap: wrap;
}

.projectCards {
  background-color: #ffd542;
  font-family: 'Sen', sans-serif;
  color: #033686;
}

.btn-deploy {
  color: #ffffff;
}

.experienceContainer {
  margin-left: 25%;
  margin-right: 20%;
  align-content: center;
}

.exp-title {
  font-family: 'Sen', sans-serif;
  color: #271e85;
}

.exp-tool {
  font-family: 'Sen', sans-serif;
  color: #271e85;
  font-weight: bolder;
}

.exp-des {
  font-family: 'Sen', sans-serif;
  color: #271e85;
}

.contact-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contactDescription {
  font-family: 'Sen', sans-serif;
  color: #271e85;
  margin-bottom: 25px;
  text-align: center;
}

.contactCard {
  font-family: 'Sen', sans-serif;
  color: #ffda59;
}

.carousel-contact {
  font-family: 'Sen', sans-serif;
}

.footer {
  padding: 20px;
  background-color: #3e35a2;
  color: #ffd542;
  align-items: flex-end;
  text-align:  center;
  font-family: 'Sen', sans-serif;
}


@media all and (max-width: 480px) {
  .home-container {
    background-color: white;
  }

  .experienceContainer {
    margin-left: 10%;
    margin-right: 10%;
    align-content: center;
  }

  .profileText {
    margin-left: 10%;
    margin-right: 10%;
    padding: 25px;
    text-align: center;
    font-size: 16px;
}


}
